// eslint-disable-next-line import/no-named-as-default
import html from 'atomico/html';
import {
  FontFamilyPrimary,
  ColorComplementaryB1,
  ColorComplementaryB2,
  ColorPrimaryP1,
  ColorPrimaryP3,
  ColorPrimaryP5,
  ColorComplementaryT8,
  ColorComplementaryT10,
  ColorContextA1,
  ColorContextH1,
  ColorContextU1,
  ColorSecondaryS6,
  SpacingPaddingP6,
  SpacingPaddingP8,
  SpacingPaddingP4,
} from '@ca-design-system/ds-tokens/build/ts/tokens';

// eslint-disable-next-line import/extensions
import { useSlots } from './hooks';
import '@ca-design-system/ds-icon';
import { css } from '@emotion/css';
import { Props } from 'atomico';

enum BackgroundColorTypes {
  white = 'white',
  green = 'green',
  grayLight = 'grayLight',
  grayDark = 'grayDark',
}

enum BoxShadowTypes {
  shadow1 = 'shadow1',
  shadow2 = 'shadow2',
}

enum BorderColorTypes {
  gray = 'gray',
  primary = 'primary',
}

enum BorderTypeTypes {
  none = 'none',
  normal1px = 'normal1px',
  highlight = 'highlight',
}

enum HighlightColorTypes {
  hospitalario = 'hospitalario',
  ambulatorio = 'ambulatorio',
  urgencia = 'urgencia',
  primary5 = 'primary5',
  secondary6 = 'secondary6',
}

const backgroundColorSwitch = (backgroundColor: BackgroundColorTypes) =>
  ({
    white: ColorComplementaryB1,
    green: ColorPrimaryP3,
    grayLight: ColorComplementaryB2,
    grayDark: ColorComplementaryT10,
  }[backgroundColor]);

const boxShadowSwitch = (boxShadow: BoxShadowTypes) =>
  ({
    shadow1: '0 2px 5px 0 rgba(201, 207, 227, 0.75)',
    shadow2: '0 6px 20px 0 rgba(178, 185, 207, 0.75)',
  }[boxShadow]);

const borderWidthSwitch = (borderType: BorderTypeTypes) =>
  ({
    none: 'none',
    normal1px: '1px',
    highlight: '0px 0px 0px 12px',
  }[borderType]);

const borderColorSwitch = (borderColor: BorderColorTypes) =>
  ({
    gray: ColorComplementaryT8,
    primary: ColorPrimaryP1,
  }[borderColor]);

const highlightColorSwitch = (highlightColor: HighlightColorTypes) =>
  ({
    hospitalario: ColorContextH1,
    ambulatorio: ColorContextA1,
    urgencia: ColorContextU1,
    primary5: ColorPrimaryP5,
    secondary6: ColorSecondaryS6,
  }[highlightColor]);

const containerStyles = (
  backgroundColor: BackgroundColorTypes,
  padding: string,
  boxShadow: BoxShadowTypes,
  borderColor: BorderColorTypes,
  roundedBorder: boolean,
  borderType: BorderTypeTypes,
  highlightColor: HighlightColorTypes
) => {
  return css`
    box-sizing: border-box;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    padding: ${padding};
    background-color: ${backgroundColorSwitch(backgroundColor)};
    border-radius: ${roundedBorder ? '8px' : '0px'};
    box-shadow: ${boxShadowSwitch(boxShadow)};
    font-family: ${FontFamilyPrimary};

    ${borderType === BorderTypeTypes.none &&
    css`
      border: none;
    `}

    ${borderType === BorderTypeTypes.normal1px &&
    css`
      border-color: ${borderColorSwitch(borderColor)};
      border-width: ${borderWidthSwitch(borderType)};
      border-style: ${borderColor === BorderColorTypes[borderColor]
        ? 'solid'
        : 'none'};
    `}

    ${borderType === BorderTypeTypes.highlight &&
    css`
      border-color: ${highlightColorSwitch(highlightColor)};
      border-width: ${borderWidthSwitch(borderType)};
      border-style: ${highlightColor === HighlightColorTypes[highlightColor]
        ? 'solid'
        : 'none'};
    `}
  `;
};

export function dsContainer({
  backgroundColor,
  padding,
  boxShadow,
  borderColor,
  roundedBorder,
  borderType,
  highlightColor,
}: Props<typeof dsContainer.props>) {
  const { children } = useSlots();
  return html`<host>
    <div
      class=${containerStyles(
        backgroundColor as BackgroundColorTypes,
        padding,
        boxShadow as BoxShadowTypes,
        borderColor as BorderColorTypes,
        roundedBorder as boolean,
        borderType as BorderTypeTypes,
        highlightColor as HighlightColorTypes
      )}
    >
      ${children.map((elem: any) => html`<${elem} />`)}
    </div>
  </host>`;
}

dsContainer.props = {
  backgroundColor: { type: String, value: 'white' },
  padding: { type: String, value: '16px' },
  boxShadow: { type: String, value: '' },
  borderColor: { type: String, value: '' },
  borderType: { type: String, value: 'none' },
  roundedBorder: { type: Boolean, value: false },
  highlightColor: { type: String, value: '' },
};
